<template>
  <div>
    <slot></slot>
    <img v-bind:src="currentValue">
    <input v-bind="$attrs" v-model="currentValue" type="hidden">
    <MaterialButton @click="getImage" value="Set Image"></MaterialButton>
  </div>
</template>

<script>
import MaterialButton from './MaterialButton';

export default {
  name: "MaterialImagePicker",
  props: {
    name: { required: true }
  },
  components: {
    MaterialButton
  },
  computed: {
    currentValue: {
      set(value) {
        this.$store.dispatch("changeOption", { name: this.name, value });
      },
      get() {
        return this.$store.getters.optionValue(this.name);
      }
    }
  },
  methods: {
    getImage() {
      const t = this;
      const custom_uploader = wp.media({
                            title: 'Insert image',
                            library: {
                                // uncomment the next line if you want to attach image to the current post
                                // uploadedTo : wp.media.view.settings.post.id,
                                type: 'image'
                            },
                            button: {
                                text: 'Use this image' // button label text
                            },
                            multiple: false // for multiple image selection set to true
                        }).on('select', function () { // it also has "open" and "close" events
                            var attachment = custom_uploader.state().get('selection').first().toJSON();
                            if(attachment.url) {
                              t.currentValue =  attachment.url;
                              console.log(attachment.url);
                            }
                        }).open();
    }
  }
};
</script>

<style lang="scss" scoped>
div {
  display: flex;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 8px;
}
input {
  outline: none;
  margin-left: 8px;
  border-radius: 4px;
  border: 1px solid #dadada;
  &:invalid {
    border: 1px solid red;
  }
  &:focus {
    border: 1px solid #1a73e8;
    &:valid {
      border: 1px solid green;
    }
  }
}
img {
      width: 80px;
    padding: 10px;
}
</style>
