<template>
  <div class="tabs-wrapper" :class="mode">
    <div class="tabs-header">
      <h2 class="logo">udesly</h2>
      <div role="tablist" class="tabs">
        <a
          v-for="(tab, i) in tabs"
          :key="i"
          class="tab-link"
          role="tab"
          @click="selectTab(i)"
          :class="{'is-active': tab.isActive}"
        >
          <i :class="tab.icon" v-if="tab.icon"></i>
          {{ tab.name }}
        </a>
      </div>
    </div>
    <div class="tabs-component-panels">
      <slot/>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tabs",
  props: {
    mode: { default: "horizontal" }
  },
  data: () => ({
    tabs: [],
    currentActiveIndex: 0
  }),
  created: function() {
    this.tabs = this.$children;
  },
  mounted: function() {
    this.selectTab(0);
  },
  methods: {
    selectTab(i) {
      this.currentActiveIndex = i;
      this.tabs.forEach(tab => (tab.isActive = false));
      this.tabs[this.currentActiveIndex].isActive = true;
    }
  }
};
</script>
<style lang="scss" scoped>
.tabs-wrapper {
  .tabs-header {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 8px;
    align-items: center;
    .logo {
      padding-left: 12px;
      padding-right: 12px;
      font-size: 20px;
    }
  }
  & > div {
    background: white;
    border-radius: 5px;
    margin-bottom: 6px;
  }
  &.vertical {
    display: grid;
    grid-template-columns: 20% 1fr;
    .tabs {
      flex-direction: column;
      border-right: 1px solid rgba(0, 0, 0, 0.12);
    }
  }
}
.tabs {
  margin: 0;
  display: flex;
  justify-content: center;
  justify-self: right;
  li:first-of-type a {
    border-top-left-radius: 4px;
  }
  li:last-of-type a {
    border-top-right-radius: 4px;
  }
  a {
    opacity: 0.7;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 26px;
    cursor: pointer;
    color: #333333;
    text-transform: capitalize;
    font-size: 14px;
    font-weight: bold;
    position: relative;
    transition: all 100ms ease-in-out;
    i {
      font-size: 18px;
      font-weight: 900;
    }
    &:hover {
      opacity: 0.85;
    }
    &.is-active {
      color: #1a73e8;
      opacity: 1;
    }
    &.is-active::before {
      background-color: #1a73e8;
      bottom: 0;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
      content: "";
      display: block;
      height: 3px;
      left: 0;
      margin: 0 8px;
      position: absolute;
      right: 0;
    }
  }
}
.tabs-component-panels {
  padding: 12px;
  background-color: #fafafa !important;
}
</style>


