<template>
  <div class="select-wrapper">
    <slot></slot>
    <div style="position:relative; margin-left: 8px;">
      <div class="option-selector" @click="toggleOpen">{{ label }}</div>
      <ul v-show="open">
        <li
          v-for="(option, v, index) in selectOptions"
          :key="v"
          @click="changeValue(index)"
          :class="{'active': v == value}"
        >{{ option }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Select",
  props: ["options", "name"],
  data: () => ({
    selectOptions: {},
    selectedIndex: 0,
    open: false
  }),
  computed: {
    value() {
      return this.$store.getters.optionValue(this.name);
    },
    label() {
      return this.selectOptions[this.value];
    }
  },
  mounted() {
    this.selectOptions = JSON.parse(this.options);
  },
  methods: {
    changeValue(index) {
      this.selectedIndex = index;
      this.$store.dispatch('changeOption', {name: this.name, value: Object.keys(this.selectOptions)[this.selectedIndex]})
      this.toggleOpen();
    },
    toggleOpen() {
      this.open = !this.open;
    }
  }
};
</script>

<style lang="scss" scoped>
.select-wrapper {
  display: flex;
  position: relative;
  align-items: center;
}
.option-selector {
  padding: 8px;
  border: 1px solid #dadada;
  border-radius: 3px;
}
ul {
  top: 38px;
  letter-spacing: 0.009375em;
  text-decoration: inherit;
  text-transform: inherit;
  line-height: 1.5rem;
  margin: 0;
  border-radius: 3px;
  padding: 8px 0;
  min-width: 150px;
  max-height: calc(48px * 4);
  overflow-y: auto;
  list-style-type: none;
  background-color: white;
  position: absolute;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
    0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  li {
    display: flex;
    position: relative;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: start;
    justify-content: flex-start;
    height: 48px;
    padding: 0 16px;
    overflow: hidden;
    font-size: 16px;
    margin-bottom: 0px;
    white-space: nowrap;
    cursor: pointer;
    &:hover,
    &.active {
      background-color: rgba(0, 0, 0, 0.17);
    }
  }
}
</style>
