<template>
  <div>
    <slot></slot>
    <input v-bind="$attrs" v-model="currentValue">
  </div>
</template>

<script>
export default {
  name: "MaterialInput",
  props: {
    name: { required: true }
  },
  computed: {
    currentValue: {
      set(value) {
        this.$store.dispatch("changeOption", { name: this.name, value });
      },
      get() {
        return this.$store.getters.optionValue(this.name);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
div {
  display: flex;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 8px;
}
input {
  outline: none;
  margin-left: 8px;
  border-radius: 4px;
  border: 1px solid #dadada;
  &:invalid {
    border: 1px solid red;
  }
  &:focus {
    border: 1px solid #1a73e8;
    &:valid {
      border: 1px solid green;
    }
  }
}
</style>
