<template>
  <div id="udesly-settings-wrapper">
    <form @submit.prevent="handleSave" id="udesly-settings-form">
    <slot></slot>
    <div class="udesly-actions">
      <MaterialButton :disabled="isButtonDisabled" :value="buttonMessage" type="submit"></MaterialButton>
    </div>
    </form>
  </div>
</template>

<script>
import Vue from "vue";
import Vuex from "vuex";
import MaterialButton from "./components/MaterialButton";
import { ajaxCall } from "./utils/ajax";
import { setTimeout } from 'timers';

Vue.use(Vuex);
const settings = JSON.parse(window.udeslySettings);

export const store = new Vuex.Store({
  state: {
    options: settings.settings || {},
    nonce: settings.nonce,
    ajaxurl: settings.ajaxurl,
    labels: settings.labels,
    buttonState: "save"
  },
  mutations: {
    CHANGE_OPTION_VALUE(state, option) {
      const [context, optionName] = option.name.split(".");
      state.options[context][optionName] = option.value;
    },
    CHANGE_BUTTONS_STATE(state, payload) {
      state.buttonState = payload;
    }
  },
  actions: {
    changeOption({ commit }, option) {
      commit("CHANGE_OPTION_VALUE", option);
    },
    saveOptions({commit, state}) {
      commit("CHANGE_BUTTONS_STATE", "saving");
      const {options, nonce, ajaxurl} = state;
       ajaxCall(
        ajaxurl,
        "save_udesly_settings",
        nonce,
        "data",
        JSON.stringify(options)
      ).then(res => {
        if (res.ok) {
           commit("CHANGE_BUTTONS_STATE", "save_success");
            setTimeout(()=> {
            commit("CHANGE_BUTTONS_STATE", "save");
          }, 2000);
        } else {
          commit("CHANGE_BUTTONS_STATE", "save_failed");
          setTimeout(()=> {
            commit("CHANGE_BUTTONS_STATE", "save");
          }, 3000);
        }
      });
    }
  },
  getters: {
    optionsState: function(state) {
      return state.options;
    },
    nonce: function(state) {
      return state.nonce;
    },
    ajaxurl: function(state) {
      return state.ajaxurl;
    },
    optionValue: state => name => {
      if (!name) {
        return "";
      }
      const [context, optionName] = name.split(".");
      return state.options[context][optionName];
    },
    buttonMessage: function(state) {
      return state.labels[state.buttonState];
    }
  }
});

export default {
  name: "app",
  store,
  data: () => ({
    invalidForm: false
  }),
  mounted: function() {
    const form = document.querySelector('#udesly-settings-form');
    this.invalidForm = !form.checkValidity();
    form.addEventListener('input', () => {
        this.invalidForm = !form.checkValidity();
    });
  },
  computed: {
    buttonMessage() {
      return this.$store.getters.buttonMessage;
    },
    isButtonDisabled() {
       return this.$store.state.buttonState !== "save" || this.invalidForm;
    }
  },
  methods: {
    handleSave() {
      this.$store.dispatch("saveOptions");
    }
  },
  components: {
    MaterialButton
  }
};
</script>

