<template>
  <div class="udesly-expansion-panel" :class="{'is-open':isOpen}">
    
    <div @click="toggle" class="udesly-expansion-panel-header">
      <slot name="header"></slot>
      <i class="fas fa-chevron-down"></i>
    </div>
    <div v-show="isOpen" class="udesly-expansion-panel-body">
      <div>
         <slot name="description"></slot>
        </div>
      <ResponsiveGrid>
      <slot></slot>
      </ResponsiveGrid>
    </div>
    
  </div>
  
</template>

<script>
import ResponsiveGrid from './ResponsiveGrid'

export default {
  name: "ExpansionPanel",
  props: ["open"],
  data: function() {
    return {
      isOpen: this.open || false
    };
  },
  methods: {
    toggle() {
      this.isOpen = !this.isOpen;
    }
  },
  components: {
    ResponsiveGrid
  }
};
</script>

<style lang="scss" scoped>
.udesly-expansion-panel {
  background: white;
  border-bottom: 1px solid #f7f7f7;
  &.is-open,
  &.is-open .udesly-expansion-panel-body {
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
  }
  &:first-of-type,
  &:first-of-type .udesly-expansion-panel-header {
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
  }
}
.udesly-expansion-panel-header {
  display: grid;
  height: 45px;
  grid-gap: 8px;
  grid-template-columns: 1fr 1fr auto;
  flex-direction: row;
  align-items: center;
  padding: 0 24px;
  background-color: white;
  justify-items: right;

  & > h3 {
    font-size: 16px;
    font-weight: bold;
    justify-self: left;
    margin: 0;
  }
  & > p {
    margin: 0;
    margin-right: 8px;
    font-style: italic;
  }
  i {
    margin: 0;
    transition: all 200ms ease-in-out;
  }
}

.is-open i {
  transform: rotate(-180deg);
}

.udesly-expansion-panel-body {
  padding: 0 24px 16px;
  background-color: white;
}
</style>
